<template>
  <view class="login-page">
    <!-- È°∂ÈÉ®LogoÂå∫Âüü -->
    <view class="login-header">
      <view class="logo">
        <text class="logo-icon">üìù</text>
      </view>
      <view class="title">ÁîüÊ¥ªËÆ∞ÂΩï</view>
      <view class="subtitle">ËÆ∞ÂΩïÁæéÂ•ΩÁîüÊ¥ªÁöÑÊØè‰∏ÄÂàª</view>
    </view>

    <!-- ÁôªÂΩïË°®Âçï -->
    <view class="login-form">
      <!-- Áî®Êà∑ÂêçËæìÂÖ• -->
      <view class="form-item">
        <view class="input-wrapper">
          <text class="icon">üë§</text>
          <input 
            v-model="username"
            type="text"
            placeholder="ËØ∑ËæìÂÖ•Áî®Êà∑Âêç"
            @input="handleInput('username', $event)"
            :focus="focusField === 'username'"
            @focus="setFocus('username')"
            @blur="setFocus('')"
          />
        </view>
      </view>

      <!-- ÂØÜÁ†ÅËæìÂÖ• -->
      <view class="form-item">
        <view class="input-wrapper">
          <text class="icon">üîí</text>
          <input 
            v-model="password"
            :type="showPassword ? 'text' : 'password'"
            placeholder="ËØ∑ËæìÂÖ•ÂØÜÁ†Å"
            @input="handleInput('password', $event)"
            :focus="focusField === 'password'"
            @focus="setFocus('password')"
            @blur="setFocus('')"
          />
          <text 
            class="toggle-btn" 
            @click="showPassword = !showPassword"
          >
            {{ showPassword ? 'ÈöêËóè' : 'ÊòæÁ§∫' }}
          </text>
        </view>
      </view>

      <!-- ÈîôËØØÊèêÁ§∫ -->
      <view v-if="errorMsg" class="error-message">
        {{ errorMsg }}
      </view>

      <!-- ÁôªÂΩïÊåâÈíÆ -->
      <button 
        class="login-btn"
        :loading="loading"
        :disabled="!canLogin || loading"
        @click="handleLogin"
      >
        ÁôªÂΩï
      </button>

      <!-- ÂÖ∂‰ªñÁôªÂΩïÊñπÂºè -->
      <view class="other-login">
        <view class="line"></view>
        <view class="text">ÂÖ∂‰ªñÁôªÂΩïÊñπÂºè</view>
        <view class="line"></view>
      </view>

      <view class="social-login">
        <button 
          class="social-btn" 
          open-type="getUserInfo" 
          @getuserinfo="handleWechatLogin"
        >
          <text class="icon">ÂæÆ‰ø°ÁôªÂΩï</text>
        </button>
      </view>
    </view>

    <!-- Â∫ïÈÉ®‰ø°ÊÅØ -->
    <view class="footer">
      <view class="test-account" v-if="showTestAccount">
        <text>ÊµãËØïË¥¶Âè∑: test / 123456</text>
        <text class="close-btn" @click="showTestAccount = false">√ó</text>
      </view>
      <view class="copyright">
        ¬© 2024 ÁîüÊ¥ªËÆ∞ÂΩï ÁâàÊùÉÊâÄÊúâ
      </view>
    </view>
  </view>
</template>

<script setup>
import { ref, computed, onMounted, onUnmounted } from 'vue'
import { useRouter } from 'vue-router'
import request from '@/utils/request'

// Ê≥®ÊÑèÔºöÁßªÈô§‰∫Ü‰ªé@dcloudio/uni-appÁöÑÂØºÂÖ•ÔºåÁõ¥Êé•‰ΩøÁî®ÂÖ®Â±ÄuniÂØπË±°

// ÂìçÂ∫îÂºèÊï∞ÊçÆ
const username = ref('')
const password = ref('')
const errorMsg = ref('')
const loading = ref(false)
const showPassword = ref(false)
const focusField = ref('')
const showTestAccount = ref(true)
const router = useRouter()

// ËÆ°ÁÆóÂ±ûÊÄß - Âà§Êñ≠ÊòØÂê¶ÂèØ‰ª•ÁôªÂΩï
const canLogin = computed(() => {
  return username.value.trim().length > 0 && password.value.trim().length > 0
})

// Â§ÑÁêÜËæìÂÖ•
const handleInput = (field, e) => {
  if (field === 'username') {
    username.value = e.detail.value.trim()
  } else {
    password.value = e.detail.value.trim()
  }
  // ËæìÂÖ•Êó∂Ê∏ÖÈô§ÈîôËØØÊèêÁ§∫
  if (errorMsg.value) {
    errorMsg.value = ''
  }
}

// ËÆæÁΩÆËæìÂÖ•Ê°ÜÁÑ¶ÁÇπÁä∂ÊÄÅ
const setFocus = (field) => {
  focusField.value = field
}

// Ê£ÄÊü•ÊòØÂê¶Â∑≤ÁôªÂΩï
const checkLoginStatus = () => {
  // Áõ¥Êé•‰ΩøÁî®uni.getStorageSyncÔºåÊó†ÈúÄÂØºÂÖ•
  const token = uni.getStorageSync('token')
  if (token) {
    // Â∑≤ÁôªÂΩïÔºåË∑≥ËΩ¨Âà∞È¶ñÈ°µ
    uni.switchTab({ url: '/pages/life/life' })
    return true
  }
  return false
}

// Ë¥¶Âè∑ÂØÜÁ†ÅÁôªÂΩï
const handleLogin = async () => {
  // ÂâçÁ´ØÈ™åËØÅ
  if (!username.value.trim()) {
    errorMsg.value = 'ËØ∑ËæìÂÖ•Áî®Êà∑Âêç'
    return
  }
  if (!password.value.trim()) {
    errorMsg.value = 'ËØ∑ËæìÂÖ•ÂØÜÁ†Å'
    return
  }

  try {
    loading.value = true
    errorMsg.value = ''

    // Ë∞ÉÁî®ÁôªÂΩïÊé•Âè£
    const res = await request.post('/api/v1/auth/login', {
      username: username.value.trim(),
      password: password.value.trim()
    })

    if (res.code === 200) {
      // ÁôªÂΩïÊàêÂäüÔºå‰øùÂ≠òtokenÂíåÁî®Êà∑‰ø°ÊÅØ
      uni.setStorageSync('token', res.data.token)
      uni.setStorageSync('userInfo', res.data.user)

      uni.showToast({
        title: 'ÁôªÂΩïÊàêÂäü',
        icon: 'success',
        duration: 1500
      })

      // Âª∂ËøüË∑≥ËΩ¨
      setTimeout(() => {
        const pages = getCurrentPages()
        if (pages.length > 1) {
          uni.navigateBack({ delta: 1 })
        } else {
          uni.switchTab({ url: '/pages/life/life' })
        }
      }, 1500)
    } else {
      errorMsg.value = res.message || 'ÁôªÂΩïÂ§±Ë¥•ÔºåËØ∑ÈáçËØï'
    }
  } catch (err) {
    errorMsg.value = err.message || 'ÁΩëÁªúÂºÇÂ∏∏ÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúËøûÊé•'
  } finally {
    loading.value = false
  }
}

// ÂæÆ‰ø°Âø´Êç∑ÁôªÂΩï
const handleWechatLogin = async (e) => {
  try {
    loading.value = true
    errorMsg.value = ''

    // Ëé∑ÂèñÁî®Êà∑‰ø°ÊÅØ
    const userInfo = e.detail.userInfo
    if (!userInfo) {
      errorMsg.value = 'ËØ∑ÊéàÊùÉÁî®Êà∑‰ø°ÊÅØ'
      return
    }

    // Ë∞ÉÁî®ÂæÆ‰ø°ÁôªÂΩïÊé•Âè£Ëé∑Âèñcode
    const loginRes = await new Promise((resolve, reject) => {
      uni.login({
        provider: 'weixin',
        success: resolve,
        fail: reject
      })
    })

    // Ë∞ÉÁî®ÂêéÁ´ØÂæÆ‰ø°ÁôªÂΩïÊé•Âè£
    const res = await request.post('/api/v1/auth/wechat-login', {
      code: loginRes.code,
      userInfo: userInfo
    })

    if (res.code === 200) {
      // ÁôªÂΩïÊàêÂäüÂ§ÑÁêÜÔºåÂêå‰∏ä
      uni.setStorageSync('token', res.data.token)
      uni.setStorageSync('userInfo', res.data.user)

      uni.showToast({
        title: 'ÁôªÂΩïÊàêÂäü',
        icon: 'success',
        duration: 1500
      })

      setTimeout(() => {
        const pages = getCurrentPages()
        if (pages.length > 1) {
          uni.navigateBack({ delta: 1 })
        } else {
          uni.switchTab({ url: '/pages/life/life' })
        }
      }, 1500)
    } else {
      errorMsg.value = res.message || 'ÂæÆ‰ø°ÁôªÂΩïÂ§±Ë¥•'
    }
  } catch (err) {
    errorMsg.value = err.message || 'ÂæÆ‰ø°ÁôªÂΩïÂºÇÂ∏∏'
  } finally {
    loading.value = false
  }
}

// È°µÈù¢ÊåÇËΩΩÊó∂Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅ
onMounted(() => {
  // Âª∂ËøüÊ£ÄÊü•ÔºåÈÅøÂÖçÈ°µÈù¢ÂàáÊç¢ËøáÂø´
  const timer = setTimeout(() => {
    checkLoginStatus()
  }, 500)
  
  onUnmounted(() => {
    clearTimeout(timer)
  })
})
</script>

